<style type="text/css">
  .group-mgt-move {
    cursor: pointer;
    align-items: center;
    display: flex;
    justify-content: center;
  }
  .group-mgt-move>img {
    height: 22px;
    width: 22px;
  }
  .group-draggable-header{
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    border-bottom: 1px solid #ebeef5;
  }
  .group-draggable-header>div{
    line-height: 23px;
    color: #222;
    font-weight: bold;
    padding: 8px 10px 8px 10px;
  }
  .group-draggable-width>div:nth-of-type(1){
    width: 7%;
    min-width: 60px;
  }
  .group-draggable-width>div:nth-of-type(2){
    width: 14%;
    min-width: 120px;
  }
  .group-draggable-width>div:nth-of-type(3){
    width: 19%;
    min-width: 160px;
  }
  .group-draggable-width>div:nth-of-type(4){
    width: 10%;
    min-width: 80px;
  }
  .group-draggable-width>div:nth-of-type(5){
    width: 40%;
    min-width: 340px;
  }
  .group-draggable-width>div:nth-of-type(6){
    width: 10%;
    min-width: 100px;
  }
  .group-draggable-td{
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    border-bottom: 1px solid #ebeef5;
  }
  .group-draggable-td>div{
    padding: 8px 10px 8px 10px;
    line-height: 30px;
  }
  .no-data-wrap {
    line-height: 60px;
    text-align: center;
  }
</style>
<template>
  <div>
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary"  size="mini" icon="el-icon-plus" @click="addGroup">新增分组</el-button>
      </div>
      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <el-form-item label="分组名称">
            <el-input class="search-input" v-model.trim="searchContent.name" placeholder="请输入名称关键字" size="small" @keyup.enter.native="doSearchHandel" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="doSearchHandel" size="mini">搜索</el-button>
          </el-form-item>

          <el-button type="primary" @click="doSortOnceHandel" size="mini" style="float:right;margin-top:26px;" v-if="activeName == 'abled' && !canMove">编辑排序</el-button>
          <el-button type="primary" @click="doSaveSortHandel" :disabled="isDisabled" size="mini" style="float:right;margin-top:26px;" v-else-if="activeName == 'abled'">保存排序</el-button>
        </el-form>
      </div>
    </div>

    <!-- 搜索结果 -->
    <div class="tab-wraper table-wraper" v-loading="loading">
      <el-tabs v-model="activeName">
        <el-tab-pane name="abled">
          <span slot="label">启用 {{this.abledTableData.length}}</span>
        </el-tab-pane>
        <el-tab-pane name="disabled">
          <span slot="label">禁用 {{this.total}}</span>
        </el-tab-pane>

        <div style="background-color: #fff;" v-show="activeName == 'abled'">
          <div class="group-draggable-header group-draggable-width">
            <div>序号</div>
            <div>名称</div>
            <div>修改时间</div>
            <div>状态</div>
            <div>操作</div>
            <div v-if="canMove"></div>
          </div>
          <!-- 可拖拽的盒子 -->
          <draggable v-model="abledTableData" v-if="canMove">
            <transition-group>
              <div v-for="(item, i) in abledTableData" :key="item.index" class="group-draggable-td group-draggable-width">
                <div>{{item.index}}</div>
                <div>{{item.name}}</div>
                <div>{{item.updateTime}}</div>
                <div>{{item.statusName}}</div>
                <div>
                  <el-button size="mini" @click="editGroup(item)">编辑</el-button>
                  <el-button size="mini" @click="deleteGroup(item)">删除</el-button>
                  <el-button size="mini" @click="disabledGroup(item)">禁用</el-button>
                  <el-button size="mini" @click="mgGroupProducts(item)">管理商品</el-button>
                </div>
                <div class="group-mgt-move" v-if="canMove">
                  <img src="/shop/static/imgs/move.png" alt="">
                  <span>按住拖拽</span>
                </div>         
              </div>
            </transition-group>
          </draggable>    
          <!-- 不可拖拽的盒子 -->
          <div v-for="(item, i) in abledTableData" :key="item.index" class="group-draggable-td group-draggable-width" v-else>
            <div>{{item.index}}</div>
            <div>{{item.name}}</div>
            <div>{{item.updateTime}}</div>
            <div>{{item.statusName}}</div>
            <div>
              <el-button size="mini" @click="editGroup(item)">编辑</el-button>
              <el-button size="mini" @click="deleteGroup(item)">删除</el-button>
              <el-button size="mini" @click="disabledGroup(item)">禁用</el-button>
              <el-button size="mini" @click="mgGroupProducts(item)">管理商品</el-button>
            </div>
            <div v-if="canMove"></div>         
          </div>

          <div v-show="abledTableData.length <= 0" class="no-data-wrap">没有查询到您想要的数据</div> 
          
        </div>

        <el-table
          class="fixed-height"
          :data="disabledTableData"
          empty-text="没有查询到您想要的数据"
          style="width:100%;"
          v-show="activeName == 'disabled'"
          size="small">
          <el-table-column min-width="60" prop="index" label="序号"></el-table-column>
          <el-table-column min-width="120" prop="name" label="名称"></el-table-column>
          <el-table-column min-width="160" prop="updateTime" label="修改时间"></el-table-column>
          <el-table-column min-width="80" prop="statusName" label="状态"></el-table-column>
          <el-table-column min-width="340" label="操作">
            <template slot-scope="scope">
              <el-button size="mini" @click="editGroup(scope.row)">编辑</el-button>
              <el-button size="mini" @click="deleteGroup(scope.row)">删除</el-button>
              <el-button size="mini" @click="disabledGroup(scope.row, 'true')">开启</el-button>
              <el-button size="mini" @click="mgGroupProducts(scope.row)">管理商品</el-button>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页条 -->
        <el-pagination
          v-show="activeName == 'disabled' && disabledTableData.length>0"
          class="tc mt20"
          background
          @size-change="changeSize"
          @current-change="changeCurrent"
          :current-page.sync="currentPage"
          :page-sizes="[20, 50, 100, 150, 200]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
        </el-pagination>
      </el-tabs>

    </div>
    
    <!-- 新增分组dialog -->
    <el-dialog title="新增分组" :visible.sync="addGroupDialogVisible" width="360px">
      <el-form :model="addGroupContent" label-width="90px" label-position="right">
        <el-form-item label="分组名称" required>
          <el-input class="search-input" v-model.trim="addGroupContent.name" placeholder="请输入分组名称" size="small" clearable></el-input>
          <div style="line-height: 18px;">分组名称限制4个字</div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addGroupDialogVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="sureAddGroup" size="mini" :disabled="isDisabledAdd">保 存</el-button>
      </span>
    </el-dialog>
    
    <!-- 编辑分组dialog -->
    <el-dialog title="编辑分组" :visible.sync="editGroupDialogVisible" width="360px">
      <el-form :model="editGroupContent" label-width="90px" label-position="right">
        <el-form-item label="分组名称" required>
          <el-input class="search-input" v-model.trim="editGroupContent.name" placeholder="请输入分组名称" size="small" clearable></el-input>
          <div style="line-height: 18px;">分组名称限制4个字</div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editGroupDialogVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="sureEditGroup" size="mini" :disabled="isDisabledEdit">保 存</el-button>
      </span>
    </el-dialog>
    
    <!-- 删除分组dialog -->
    <el-dialog title="删除分组" :visible.sync="deleteGroupDialogVisible" width="360px">
      <span>您确定要删除所选分组吗？</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteGroupDialogVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="sureDeleteGroup" size="mini">确 认</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import Storage from '@/utils/localStorage'
import Draggable from 'vuedraggable'
import * as utils from '@/utils/utils'
 
const groupMgtUrl = {
  // 获取分组列表
  list: '/plateform/a/group/list',
  // 新增分组
  add: '/plateform/a/group/add',
  // 分组排序
  sort: '/plateform/a/group/sort',
  // 编辑分组
  update: '/plateform/a/group/update',
  // 删除分组
  del: '/plateform/a/group/del',
  // 启用禁用分组
  updateStatus: '/plateform/a/group/updateStatus'
}
export default {
  data() {
    return {
      loading: false,
      isDisabled: false, // 按钮是否可点击
      isDisabledAdd: false, // 保存按钮是否可点击
      isDisabledEdit: false, // 编辑按钮是否可点击
      canMove: false, // 是否可以拖拽移动
      addGroupDialogVisible: false, // 新增
      editGroupDialogVisible: false, // 编辑
      deleteGroupDialogVisible: false, // 删除
      activeName: 'abled', // 启用
      currentPage: 1,
      pageSize: 20,
      total: 0,
      searchContent: { 
        name: '', // 分组名字
        status: '' // 状态
      },
      abledTableData: [], // 启用列表
      disabledTableData: [], // 禁用列表
      addGroupContent: { // 新增分组
        name: '' // 分组名字
      },
      editGroupContent: { // 编辑分组
        name: '', // 分组名字
        id: ''  // ID名
      },
      deleteGroupContent: { // 删除分组
        id: '',  // ID名
        isDeleted: '' // 删除状态
      }
    }
  },
  mounted() {
    this.doSearchHandel(true)
  },
  components: {
    'draggable': Draggable
  },
  methods: {
    /**
     * [changeSize description]
     * @param    {[type]}                 value [description]
     * @return   {[type]}                       [description]
     */
    changeSize (value) {
      this.pageSize = value
      this.doSearchHandel()
    },
    /**
     * [changeCurrent 点击分页条]
     * @param    {[type]}                 value [当前页]
     * @return   {[type]}                       [description]
     */
    changeCurrent (value) {
      this.doSearchHandel(value, 'changeCurrent')
    },
    /**
     * [doSearchHandel 搜索]
     * @return {[type]} [description]
     */
    doSearchHandel(val, str) {
      this.loading = true
      if(str == 'changeCurrent') {
        this.currentPage = val
      }else{
        this.currentPage = 1
      }

      // 搜索启用
      this.searchContent.status = 0

      this.$http.post(groupMgtUrl.list, { ...this.searchContent, pageNum: 1, pageSize: 400}).then(res => {
        this.loading = false
        if(res.code === 0) {
          if(res.data && res.data.list && res.data.list.length) {
            res.data.list.forEach((item, i) => {
              item.index = i + 1
            })
            this.abledTableData = res.data.list
            this.addGroupContent.name = ''

          }else {
            this.abledTableData = []
          }

          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })

      // 搜索禁用
      this.searchContent.status = 1

      this.$http.post(groupMgtUrl.list, { ...this.searchContent, pageNum: this.currentPage, pageSize: this.pageSize}).then(res => {
        this.loading = false
        if(res.code === 0) {
          if(res.data && res.data.list && res.data.list.length) {
            res.data.list.forEach((item, i) => {
              item.index = (this.currentPage - 1) * this.pageSize + i + 1
            })
            this.disabledTableData = res.data.list

          }else {
            this.disabledTableData = []
          }
          this.total = res.data.total
          return 
        }
        throw new Error(res.msg)
      }).catch(e => {
        console.log("提示(获取禁用分组列表):", e.message)
      })
    },
    /**
     * [doSortOnceHandel 点击重新排序按钮触发事件]
     * @return {[type]} [description]
     */
    doSortOnceHandel() {
      if(this.abledTableData.length == 0) {
        return
      }
      this.canMove = true
    },
    /**
     * [doSaveSortHandel 点击保存排序按钮触发事件]
     * @return {[type]} [description]
     */
    doSaveSortHandel() {
      if(this.isDisabled === true) {
        return
      }

      let abledTableData = []
      // 组装整洁参数
      this.abledTableData.forEach((item, i) => {
        let itemObj = {
          id: item.id,
          level: i + 1
        }
        abledTableData.push(itemObj)
      })
      // 保存按钮不可再次点击
      this.isDisabled = true
      utils.showLoading()
      this.$http.post(groupMgtUrl.sort, {groupLevelList: abledTableData}).then(res => {
        // 保存按钮可再次点击
        let _this = this
        setTimeout(function() {
          _this.isDisabled = false
        }, 800)
        utils.clearLoading()
        if(res.code === 0) {
          this.$message.success("重新排序成功")
          this.canMove = false
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        // 保存按钮可再次点击
        let _this = this
        setTimeout(function() {
          _this.isDisabled = false
        }, 800)
        utils.clearLoading()
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [addGroup 新增分组]
     * @return {[type]} [description]
     */
    addGroup() {
      this.addGroupDialogVisible = true
    },
    /**
     * [sureAddGroup 确认新增分组]
     * @return {[type]} [description]
     */
    sureAddGroup() {
      if(this.addGroupContent.name == '') {
        this.$message('分组名称不能为空')
        return
      }
      if(this.addGroupContent.name.length > 4) {
        this.$message('分组名称限制4位')
        return
      }
      this.isDisabledAdd = true
      this.$http.post(groupMgtUrl.add, { ...this.addGroupContent}).then(res => {
        if(res.code === 0) {
          this.addGroupDialogVisible = false
          this.isDisabledAdd = false
          this.$message({
            message: '添加分组成功',
            type: 'success'
          })
          // 查询。获取全部状态
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.isDisabledAdd = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [editGroup 编辑分组]
     * @return {[type]} [description]
     */
    editGroup(val) {
      this.editGroupContent.name = val.name
      this.editGroupContent.id = val.id
      this.editGroupDialogVisible = true
    },
    /**
     * [sureEditGroup 确认编辑分组]
     * @return {[type]} [description]
     */
    sureEditGroup() {
      if(this.editGroupContent.name == '') {
        this.$message('分组名称不能为空')
        return
      }

      if(this.editGroupContent.name.length > 4) {
        this.$message('分组名称限制4位')
        return
      }
      this.isDisabledEdit = true
      this.$http.post(groupMgtUrl.update, { ...this.editGroupContent}).then(res => {
        if(res.code === 0) {
          this.editGroupDialogVisible = false
          this.isDisabledEdit = false
          this.$message({
            message: '编辑分组成功',
            type: 'success'
          })
          // 查询。获取全部状态
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.isDisabledEdit = false
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [deleteGroup 删除分组]
     * @return {[type]} [description]
     */
    deleteGroup(val) {
      this.deleteGroupContent.isDeleted = 1
      this.deleteGroupContent.id = val.id

      this.deleteGroupDialogVisible = true

    },
    /**
     * [sureDeleteGroup 确认删除分组]
     * @return {[type]} [description]
     */
    sureDeleteGroup() {
      this.$http.post(groupMgtUrl.del, { ...this.deleteGroupContent}).then(res => {
        if(res.code === 0) {
          this.deleteGroupDialogVisible = false
          this.$message({
            message: '删除分组成功',
            type: 'success'
          })
          // 查询。获取全部状态
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [disabledGroup 禁用分组]
     * @return {[type]} [description]
     */
    disabledGroup(val, states) {
      let disableGroupContent
      if(states == 'true') {
        disableGroupContent = {
          id: val.id,
          status: 0
        }
      }else{
        disableGroupContent = {
          id: val.id,
          status: 1
        }
      }
      this.$http.post(groupMgtUrl.updateStatus, { ...disableGroupContent}).then(res => {
        if(res.code === 0) {      
          if(states == 'true') {
            this.$message({
              message: '开启分组成功',
              type: 'success'
            })
          }else{
            this.$message({
              message: '禁用分组成功',
              type: 'success'
            })
          }
          // 查询。获取全部状态
          this.doSearchHandel()
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.$notify.error({
          title: '提示',
          message: e.message
        })
      })
    },
    /**
     * [mgGroupProducts 分组商品管理]
     * @return {[type]} [description]
     */
    mgGroupProducts(val) {
      let cityName = Storage.get("cityName")
      Storage.set('groupProductsCityName', cityName)
      Storage.set('groupProducts', val)
      this.$router.push('/group_mgt/mg_groupProducts')
    }

  }

}
</script>


